import React, { useEffect, useState} from "react"
import { loadPost, createPost, createACtionBtn, loadPostDetail } from "../lookup"


export function TextAreaModel(){
  return(
    <div className="container mt-5">
      <div className="row">
        <div className="col-12 col-md-6 col-sm-12 mx-auto">
          <div className="mr-auto">
             <button type="button" className="btn btn-primary me-auto rounded-circle" data-bs-toggle="modal" data-bs-target="#exampleModal">
              +
            </button>
          </div>
        </div>
      </div>
    
    </div>
  )
}


export function PostCompontent(props){
  const canPost = props.canPost === "false" ? false:true
  console.log(canPost)

  const imageInputRef = React.createRef()
  const textInputRef = React.createRef()
  const textAreaRef = React.createRef()
  const [newPosts, setNewPosts] = useState([])


  const handleCreateCallback = (response, status) =>{
    let tempNewPosts = [...newPosts]
    if(status===201){
      tempNewPosts.unshift(response)
      setNewPosts(tempNewPosts)
    }else{
      alert("An error occured please try again letter !")
    }
  }

const handleSubmit = (event) =>{
    event.preventDefault()
    const newVal = imageInputRef.current.files[0]
    const newContent = textAreaRef.current.value
    const newTitle = textInputRef.current.value

    let formdata = new FormData()
    formdata.append('title', newTitle)
    formdata.append('content', newContent)
    formdata.append('image', newVal)

    createPost(formdata, handleCreateCallback)


    textAreaRef.current.value = ""
    textInputRef.current.value = ""
    imageInputRef.current.value = null

  }
 

  return(
    <div>
      {canPost && <TextAreaModel />}
      <div className="modal fade" id="exampleModal" aria-labelledby="exampleModalLabel" aria-hidden="true">
            <div className="modal-dialog">
              <div className="modal-content">
                <div className="modal-header">
                  <h5 className="modal-title" id="exampleModalLabel">Post</h5>
                  <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
                </div>
                <div className="modal-body">
                  <form onSubmit={handleSubmit}>
                    <div className="mb-3">
                      <label className="form-label">Title</label>
                      <input type="text" className="form-control" ref={textInputRef}   placeholder="title" />
                    </div>
                    <div className="mb-3">
                      <label className="form-label">Content </label>
                      <textarea className="form-control"  rows="3" ref={textAreaRef} ></textarea>
                    </div>

                    <div className="mb-3">
                      <label  className="form-label">Upload file</label>
                      <input className="form-control" type="file" ref={imageInputRef} />
                    </div>

                    <div className="modal-footer">
                      <button type="button" className="btn btn-secondary" data-bs-dismiss="modal">Close</button>
                      <button type="submit" className="btn btn-primary">Post</button>
                    </div>
                </form>  
                </div>
              </div>
            </div>
          </div>
        <PostList  newPosts={newPosts} {...props}/>
    </div>
  )
}

export function PostList(props){
  const {username} = props
  const [postsInits, setPostInits] = useState([])
  const [posts, setPosts] = useState([])

  useEffect(()=>{
    let final = [...props.newPosts].concat(postsInits)
    if (final.length !== posts.length){
      setPosts(final)
    }
  }, [posts, props.newPosts, postsInits])

  useEffect(()=>{
    const myCallback = (response, status) =>{
      if(status === 200){
        setPostInits(response)
      }
    }
    loadPost(username, myCallback)
  }, [username])


  return (
    <div className="container mt-5">
      {posts.map((post, index)=>{
        return <Post post={post}  key={`${index}`}/>
      })}
    </div>
  );
}



function Post(props){
    const {post} = props
    const [actionPost, setActionPost] = useState(props.post ? props.post : null)

    const path = window.location.pathname
    const idRegEx = /(?<postId>\d+)/
    const match = path.match(idRegEx)
    const postId = match ? match.groups.postId : -1
    

    const isDetail = `${post.id}` === `${postId}` ? false:true

    const handleDidActionLike = (newActionPost) =>{
      setActionPost(newActionPost)
    }

    const handleLink =(event)=>{
      event.preventDefault()
      window.location.href = `/${post.id}`
    }


    return(
      <div className='row mt-3' >
        <div className="col-12 col-md-6 col-sm-12 mx-auto">
          <div className="card h-100">
            <img src={`http://127.0.0.1:8000/${post.image}/`} className="card-img-top" alt="..." />
            <div className="card-body">
              <h5 className="card-title">{post.title}</h5>
              <p className="card-text">{post.content}</p>
              <div>
               <small className="text-muted">{post.timestamp}</small>
              </div>
              
              <div className="btn btn-group">
                {actionPost && <React.Fragment>
                  <ActionPostBtn post={actionPost} didLike={handleDidActionLike}  action={{type:"like", display: "like"}}/>
                  <ActionPostBtn post={actionPost} didLike={handleDidActionLike}  action={{type:"unlike", display: "unlike"}}/>
                </React.Fragment>}
                  {isDetail === false ? null : <button className="btn btn-outline-primary btn-sm" onClick={handleLink}>views the post</button>}
                </div>
            </div>
          </div>
          </div>
      </div> 
    )
  }


  function ActionPostBtn(props){
    const {post, action, didLike} = props
    const likes = post.like ? post.like : 0

    const className = props.className ? props.className : "btn btn-primary btn-sm"
    const actionDisplay = action.display ? action.display : 'Action'
    const display = action.type === "like" ? `${likes} ${actionDisplay}` : actionDisplay

    const handleClick = (event) =>{
      event.preventDefault()

      const handleBackEndCallback = (response, status) =>{
        if((status === 200 || status === 201) && didLike){
          didLike(response)
        }
      }
      createACtionBtn({id:post.id, action:action.type}, handleBackEndCallback)
    }
    return(
        <button className={className} onClick={handleClick}>{display}</button>      
    )
  }

export function PostCompontentDetail(props){
  const postId = props.postId
  const [post, setPost] = useState(null)

  const handleBackendPostDetail = (response, status) =>{
    if(status===200){
      setPost(response)
    }
  }

  loadPostDetail(postId, handleBackendPostDetail)
  return post !== null ? <Post post={post} /> : null
}
  

