import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import { PostCompontent } from './post';
import { PostCompontentDetail } from './post/Compontents';
import reportWebVitals from './reportWebVitals';

const e = React.createElement
const rootEl = document.getElementById('root')
if(rootEl){
  ReactDOM.render(e(PostCompontent, rootEl.dataset), rootEl);
}

const postDetailEl = document.querySelectorAll(".post-detail-el")
postDetailEl.forEach(container=>{
  if(postDetailEl){
    ReactDOM.render(e(PostCompontentDetail, container.dataset), container)
  }
})

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
